//
// Colors
//

$colors: (
  "red": $red,
  "blue": $blue,
  "green": $green,
  "orange": $orange
);

@each $colorName, $color in $colors {
  .color-#{$colorName} {
    color: $color;
  }
}

//
// Margins
//

$directions: "top", "right", "bottom", "left";
$sizes: (
  "xxs": $space-xxs,
  "xs": $space-xs,
  "s": $space-s,
  "m": $space-m,
  "l": $space-l,
  "xl": $space-xl,
  "xxl": $space-xxl
);

@each $sizeName, $size in $sizes {
  @each $dir in $directions {
    .margin-#{$dir}-#{$sizeName} {
      margin-#{$dir}: $size !important;
    }
  }
}

//
// Widths
//

$sizes: (
  "s": 180px,
  "m": 320px,
  "l": 420px
);

@each $sizeName, $size in $sizes {
  .width-#{$sizeName} {
    width: $size;
  }
}

//
// Text
//

.uppercase {
  text-transform: uppercase;
}

.lowercase {
  text-transform: lowercase;
}

.nowrap {
  white-space: nowrap;
}

.text-small {
  font-size: $size-text-small;
}

.text-summary {
  font-size: $size-text-small;
  font-weight: 400;
  color: $color-disabled;
}

.text-sub {
  font-size: $size-text-small;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  color: $color-disabled;
  font-weight: 400;
  margin: 0 $space-xs;
}

//
// Visibility
//

.hidden {
  display: none !important;
}

.invisible {
  visibility: hidden !important;
}

//
// Inputs
//

.input-text-right {
  text-align: right;
  
  > input[type=text] {
    text-align: right;
  }
}

//
// Transform
//

.rotate-90 {
  transform: rotate(90deg);
}