.list-trunk {
  list-style: none;
  margin: 0;

  li + li {
    margin-top: $space-xs;
  }

  &.-active {
    .list-trunk-toggler[data-trunk=more] {
      display: none;
    }
  }

  &:not(.-active) {
    .list-trunk-toggler[data-trunk=less] {
      display: none;
    }
    
    li:not(:first-child) {
      display: none;
    }
  }
}

.list-trunk-toggler {
  font-size: $size-text-small;
  color: $color-link;
  text-decoration: underline;
  cursor: pointer;
}