.chips {
  display: flex;
  flex-flow: row wrap;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 0 8px 8px 0;
  max-width: 744px;
  min-height: 44px;
  cursor: text;
  transition: border linear .2s, box-shadow linear .2s;

  .chip {
    margin-top: 8px;
    margin-left: 8px;
  }

  input.chip-input {
    margin-top: 6px;
    padding-left: 12px;
  }

  &:hover {
    border-color: $color-input-border-focus;
  }

  &.-focus {
    border-color: $color-input-border-focus;
    box-shadow: inset 0 1px 1px rgba(0 ,0 , 0, .075), 0 0 8px rgba(82, 168, 236, .6);
  }

  &.-error {
    border-color: $color-error;

    &.-focus {
      border-color: $color-error-focus;
      box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075),0 0 8px $color-error-focus-shadow;
    }

    .chip-input-wrapper:after {
      display: block;
    }
  }
}

.chip {
  display: inline-block;
  font-size: $size-text-small;
  background-color: #fff;
  border: 1px solid #ddd;
  color: $grey-dark;
  font-weight: 500;
  padding: 0 12px;
  border-radius: 26px;
  line-height: 24px;
  height: 26px;
  cursor: pointer;

  &.-editable {
    user-select: none;
  }
}

.chip-action {
  display: inline-block;
  height: 26px;
  text-align: center;
  padding: 0 3px;
  font-family: 'Font Awesome 5 Free';
  opacity: .5;
  font-weight: 900;
  font-style: normal;
  margin-left: 3px;

  &:hover {
    opacity: 1;
  }

  &[data-chip-action=edit]:after {
    content: '\f304';
  }

  &[data-chip-action=remove]:after {
    position: relative;
    content: '\f00d';
    top: 1px;
  }
}

input.chip-input {
  display: inline-block;
  width: 284px;
  height: 26px;
  margin-bottom: 0; // override bootstrap.min styles
  margin-left: 3px;
  padding-top: 6px;
  border: none;
  box-shadow: none;

  &:focus {
    box-shadow: none;
  }
}

.chip-input-wrapper {
  &:after {
    display: none;
    content: attr(data-error-msg);
    color: $color-error;
    font-size: $size-text-small;
    padding-left: 14px;
  }
}